.sidenav {
    position: fixed;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    z-index: 3;
}

.sidenav__logo {
    width: 120px;
    margin: 25px;
}

.insta__logo {
    display: none;
}

.sidenav__buttons {
    display: flex;
    flex-direction: column;
}

.sidenav__button {
    display: flex;
    flex-direction: row;
    align-items: center;
    color: white;
    background: 0;
    border: 0;
    margin: 5px 10px;
    padding: 5px 10px;
    border-radius: 15px;
}

.sidenav__button:hover {
    cursor: pointer;
    background-color: rgba(255, 255, 255, 0.128);
}

.sidenav__button>span {
    font-size: 18px;
    font-weight: bolder;
    margin-left: 10px;
}

.sidenav__more {
    position: fixed;
    bottom: 3px;
}

.logout__button {
    font-size: 15px;
    border-radius: 10px;
    font-weight: bold;
    color: white;
    background-color: rgba(255, 255, 255, 0.211);
    padding: 10px;
    margin: 5px 10px;
    border: 0px;

}

.logout__button:hover {
    cursor: pointer;
    background-color: rgba(255, 255, 255, 0.424);
}

.hide {
    display: flex;
    flex-direction: column;
    display: inline-flex;
    margin: 0 0 0 15px;
    padding: 5px;
    z-index: 1;
    background: 0;
}

.hide__button {
    padding: 10px;
    margin: 1px;
    border-radius: 6px;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.hide__button:hover {
    cursor: pointer;
    background-color: rgba(255, 255, 255, 0.224);

}

.hide__button span {
    font-size: 15px;
    text-align: center;
    margin-left: 5px;
}

@media screen and (max-width: 899px) {
    .sidenav {
        min-width: 40px;
        width: 40px;
    }

    .sidenav__logo {
        display: none;
    }

    .insta__logo {
        display: flex;
        margin-top: 9px;
    }

    .sidenav__button>span {
        display: none;
    }
}

@media screen and (max-width: 599px) {
    .sidenav {
        display: flex;
        width: 100%;
        bottom: 0px;
        background-color: black;
        padding: 10px 0 10px 0;
    }

    .insta__logo {
        display: none;
    }

    .sidenav__buttons {
        flex-direction: row;
        justify-content: space-between;
        padding-bottom: 5px;
    }

    .sidenav__more {
        display: none;
    }

    #search {
        display: none;
    }

    #dm {
        display: none;
    }

    #liked {
        display: none;
    }
}